html {
  box-sizing: border-box;
  font-size: 62.5%;
}

*,
*::before,
*::after {
  box-sizing: inherit;
  margin: 0;
}

body {
  font-size: 1.6rem;
  font-family: "Manrope", sans-serif;
}

img {
  display: block;
  max-width: 100%;
}

.bg-dark-blue {
  background-color: hsl(218, 23%, 16%);
}

.vh-100 {
  min-height: 100vh;
}

.center {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.advice {
  background-color: hsl(217, 19%, 24%);
  padding: 4rem 2.5rem 6rem;
  max-width: 95%;
  border-radius: 1.5rem;
}
.advice__number {
  text-align: center;
  text-transform: uppercase;
  color: hsl(150, 100%, 66%);
}
.advice__advice {
  font-size: 28px;
  text-align: center;
  color: hsl(193, 38%, 86%);
  margin: 3.5rem 0;
}
.advice__divider {
  width: 100%;
}

.btn {
  display: flex;
  justify-content: center;
}
.btn__btn {
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
  border: none;
  padding: 2rem;
  background-color: hsl(150, 100%, 66%);
  margin: 3rem 0 -9rem;
  cursor: pointer;
}