@use "base/variables" as v;
@use "base/mixins" as m;

.advice {
  background-color: v.$dark-grayish-blue;
  padding: 4rem 2.5rem 6rem;
  max-width: 95%;
  border-radius: 1.5rem;

  &__number {
    text-align: center;
    text-transform: uppercase;
    color: v.$neo-green;
  }

  &__advice {
    font-size: v.$font-size-quote;
    text-align: center;
    color: v.$light-cyan;
    margin: 3.5rem 0;
  }

  &__divider {
    width: 100%;
  }
}

.btn {
  display: flex;
  justify-content: center;

  &__btn {
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 50%;
    border: none;
    padding: 2rem;
    background-color: v.$neo-green;
    margin: 3rem 0 -9rem;
    cursor: pointer;
  }
}
